import { useState } from 'react';
import { Button, Form, Table, Modal, FormControl, FormGroup } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import { walletSlice } from '../wallet/walletSlice';

const SupplyButton = (props) => {
  const [show, setShow] = useState(false);
  const [confirming, setconfirming] = useState(false)
  const [tab, settab] = useState('supply')
  const dispatch = useDispatch()

  const handleClose = () => {
    setShow(false);
    setAmountToSupply(0);
    setAmountToWithdraw(0);
  }
  const handleShow = () => setShow(true);

  const Tabs = () => (
    <ul className="nav nav-tabs">
      <li className="nav-item">
        <a className={"nav-link" + (tab === "supply" ? " active" : "")} href="#" onClick={() => settab("supply")}>Supply</a>
      </li>
      <li className="nav-item">
        <a className={"nav-link" + (tab === "withdraw" ? " active" : "")} href="#" onClick={() => settab("withdraw")}>Withdraw</a>
      </li>
    </ul>
  )

  let detailRows = (
    <>
      
      <tr>
        <td>Supply APY</td>
        <td>{props.market.asset.supplyApy}</td>
      </tr>
      <tr>
        <td>Borrow Limit</td>
        <td>?</td>
      </tr>
      <tr>
        <td>Borrow Limit Used</td>
        <td>?</td>
      </tr>
    </>
  )
  
  const [amountToSupply, setAmountToSupply] = useState(0)
  const onAmountToSupply = e => setAmountToSupply(e.target.value)
  const isSupplyConfirmDisabled = !!!amountToSupply || amountToSupply <= 0 || amountToSupply > props.walletAsset.balance
  const handleSupplyConfirm = () => {
    setconfirming(true);
    setTimeout(() => {
      dispatch(walletSlice.actions.setSupply({ amount: parseInt(amountToSupply), asset: props.walletAsset}))
      setShow(false)
      setconfirming(false)
      setAmountToSupply(0)
    }, 500);
  }
  const supplyTab = (
    <>
      <Modal.Body>
        <Tabs />
        <Form>
          <FormGroup>
            <Form.Label>Amount</Form.Label>
            <FormControl type="text" placeholder="Amount" value={amountToSupply} onChange={onAmountToSupply} />
          </FormGroup>
        </Form>
        <Table>
          <tbody>
            {detailRows}
            <tr>
              <td>Wallet Balance</td>
              <td>{props.walletAsset.balance}</td>
            </tr>
          </tbody>
        </Table>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          Cancel
        </Button>
        <Button variant="primary" disabled={isSupplyConfirmDisabled} onClick={handleSupplyConfirm}>
          Supply
        </Button>
      </Modal.Footer>
      
    </>
  )

  const [amountToWithdraw, setAmountToWithdraw] = useState(0)
  const onAmountToWithdraw = e => setAmountToWithdraw(e.target.value)
  const isWithdrawConfirmDisabled = !!!amountToWithdraw || amountToWithdraw <= 0 || amountToWithdraw > props.walletAsset.currentSupply
  const handleWithdrawConfirm = () => {
    setconfirming(true);
    setTimeout(() => {
      dispatch(walletSlice.actions.withdrawSupply({ amount: parseInt(amountToWithdraw), asset: props.walletAsset}))
      setShow(false)
      setconfirming(false)
      setAmountToWithdraw(0)
    }, 500);
  }
  const withdrawTab = (
    <>
      <Modal.Body>
        <Tabs />
        <Form>
          <FormGroup>
            <Form.Label>Amount</Form.Label>
            <FormControl type="text" placeholder="Amount" value={amountToWithdraw} onChange={onAmountToWithdraw} />
          </FormGroup>
        </Form>
        <Table>
          <tbody>
            {detailRows}
            <tr>
              <td>Currently Supplying</td>
              <td>{props.walletAsset.currentSupply}</td>
            </tr>
          </tbody>
        </Table>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          Cancel
        </Button>
        <Button variant="primary" disabled={isWithdrawConfirmDisabled} onClick={handleWithdrawConfirm}>
          Withdraw
        </Button>
      </Modal.Footer>
    </>
  )

  const ConfirmingModal = () => (
    <Modal show={show} onHide={handleClose}>
      <Modal.Header closeButton>
        <Modal.Title>Supply {props.market.asset}</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div>Confirming...</div>
      </Modal.Body>
      <Modal.Footer>
        
      </Modal.Footer>
    </Modal>
  )

  return (
    <>
      <Button variant="primary" onClick={handleShow}>
        Supply
      </Button>

      {confirming ? 
        <ConfirmingModal />
        : 
        (
          <Modal show={show} onHide={handleClose}>
            <Modal.Header className="justify-content-center" closeButton>
              <Modal.Title>{props.market.asset}</Modal.Title>
            </Modal.Header>
            
            {tab === "supply" ? supplyTab : withdrawTab}
            
          </Modal>
        )
      }

      
    </>
  );
}

export default SupplyButton