import React from 'react'
import { useSelector } from 'react-redux'
import Table from 'react-bootstrap/Table'
import BorrowMarket from './BorrowMarket'

const Borrow = () => {
  const markets = useSelector(state => state.market.markets)
  const totalBorrow = 0
  return (
    <>
      <div 
        className="text-center"
        style={{
          height: "100px"
        }}
      >
        <h4>Borrow Balance</h4>
        <h5>${totalBorrow.toFixed(6)}</h5>
      </div>
      <h4>Borrow Markets</h4>
      <Table>
        <thead>
          <tr>
            <th>Assets</th>
            <th>APY</th>
            <th>Wallet</th>
            <th>Liquidity</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {markets.map(market => <BorrowMarket key={market.asset} market={market} />)}
        </tbody>
      </Table>
    </>
  )
}

export default Borrow