import { useState } from 'react';
import { Button, Form, Navbar, Table, Modal, FormControl, FormGroup } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import BorrowButton from './BorrowButton'

const BorrowMarket = props => {
  const dispatch = useDispatch()
  const wallet = useSelector(state => state.wallet)
  const getWalletAsset = (assetName) => wallet.assets.find(a => a.name == assetName)
  const walletAsset = getWalletAsset(props.market.asset)

  return (
    <tr>
      <td>{props.market.asset}</td>
      <td>{props.market.APY}%</td>
      <td>{props.market.wallet} {props.market.asset}</td>
      <td>$&nbsp;{props.market.liquidity}</td>
      <td><BorrowButton market={props.market} walletAsset={walletAsset} /></td>
    </tr>
  )
}

export default BorrowMarket