import { Table } from 'react-bootstrap';
import { useSelector } from 'react-redux';
import SupplyMarket from './SupplyMarket'

const Supply = () => {
  const supply = useSelector(state => state.supply)
  const wallet = useSelector(state => state.wallet)
  
  const suppliesInUsd = wallet.assets.map(asset => asset.currentSupply * supply.markets.find(s => s.asset === asset.name).usdExchange)
  const totalSupply = suppliesInUsd.reduce((acc, usd) => acc + usd, 0)

  let marketRows = supply.markets.map(market => <SupplyMarket key={market.asset} market={market} />)
  return (
    <>
      <div 
        className="text-center"
        style={{
          height: "100px"
        }}
      >
        <h4>Supply Balance</h4>
        <h5>${totalSupply.toFixed(6)}</h5>
      </div>
      <h4>Supply Markets</h4>
      <Table>
        <thead>
          <tr>
            <th>Asset</th>
            <th>APY</th>
            <th>Wallet</th>
            <th>Collateral</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {marketRows}
        </tbody>
      </Table>
    </>
  )
}

export default Supply